import React from "react";

// import components
import {LinkButton} from "../Components";

const NotFoundPage = () => {
	return (
		<div style={styles["not__found-container"]}>
			<div>
				<h1 style={styles.h1}>Trang ban tim khng tn tai!</h1>
				<h4 style={styles.h4}>
					Xin li, chung ti khng tim thy trang ban yu cu <br /> Nu ban
					thy thiu gi o, hay th{" "}
					<a
						href="https://www.facebook.com/sipp.minhh"
						target="_blank"
						style={styles.anchor}
					>
						lin lac vi chung ti
					</a>
				</h4>
				<LinkButton className="sign-out" to="../">
					Quay lai trang trc
				</LinkButton>
			</div>
		</div>
	);
};

export default NotFoundPage;

const styles = {
	"not__found-container": {
		display: "flex",
		height: "100%",
		textAlign: "center",
		justifyContent: "center",
		alignItems: "center",
	},
	h1: {
		marginBottom: 30,
	},
	h4: {
		marginBottom: 20,
		fontWeight: 400,
	},
	anchor: {
		color: "#cccccc",
		fontSize: "inherit",
		textDecoration: "underline",
	},
};
