import React, { useState, useEffect } from "react";

// import components
import { Button } from "../../Components";
import { Form } from "semantic-ui-react";

const FormFaculties = ({
	currentFacultyId,
	facultiesObj,
	handleOnAdd,
	handleOnModify,
	setCurrentFacultyId,
}) => {
	const initialState = {
		"faculty-id": "",
		"faculty-name": "",
		"faculty-note": "",
	};

	const [values, setValues] = useState(initialState);

	useEffect(() => {
		if (currentFacultyId == "") {
			setValues({ ...initialState });
		} else {
			setValues({ ...facultiesObj[currentFacultyId] });
		}
	}, [currentFacultyId, facultiesObj]);

	// useEffect(() => {
	// 	console.log(values);
	// }, [values]);

	const handleInputChange = (e) => {
		var { name, value } = e.target;
		setValues({ ...values, [name]: value });
	};

	const handleButtonAdd = async (e) => {
		e.preventDefault();
		try {
			await handleOnAdd({ values });
			setValues(initialState);
		} catch (e) {
			return;
		}
	};

	const handleButtonModify = async (e) => {
		e.preventDefault();
		try {
			await handleOnModify(values);
			setValues(initialState);
		} catch (e) {
			return;
		}
	};

	return (
		<>
			<Form id="manage__form">
				<Form.Input
					label="Tn khoa"
					title="Tn khoa"
					className="left-align"
					placeholder="Tn khoa"
					type="text"
					name="faculty-name"
					value={values["faculty-name"]}
					onChange={handleInputChange}
					required
					autoFocus
					autoComplete="off"
				/>
				<Form.TextArea
					label="Ghi ch"
					title="Ghi ch"
					name="faculty-note"
					placeholder="Ghi Chu"
					style={{ marginBottom: 10 }}
					cols="20"
					rows="10"
					value={values["faculty-note"]}
					onChange={handleInputChange}
				/>
				<div style={{ display: "flex", justifyContent: "space-between" }}>
					<div>
						{currentFacultyId ? (
							<Button
								style={{ margin: "0" }}
								type="submit"
								className="new"
								title="Chnh sa khoa"
								onClick={handleButtonModify}
							>
								Chnh Sa
							</Button>
						) : (
							<Button
								style={{ margin: "0" }}
								type="submit"
								className="new"
								title="Thm khoa mi vo h thng"
								onClick={handleButtonAdd}
							>
								Thm
							</Button>
						)}
					</div>
					<div>
						{currentFacultyId && (
							<Button
								style={{ margin: "0" }}
								className="sign-out"
								title="Hy chnh sa"
								onClick={() => {
									setCurrentFacultyId("");
									setValues(initialState);
								}}
							>
								Hy
							</Button>
						)}
					</div>
				</div>
			</Form>
		</>
	);
};

export default FormFaculties;
