import React, { useContext, useEffect, useState } from "react";

// import components
import FormClassManage from "./FormClassManage";
import {
  getAllFaculties,
  getAllClasses,
  newClass,
  removeClass,
  modifyClass,
} from "../../../firebase";
import {
  defaultFailCB,
  readExcel,
  exists,
  getFacID,
  defaultSuccessCB,
} from "../../../utils";
import { confirmAlert } from "react-confirm-alert";
import { Loading, Button, FileDropzone } from "../../Components";

// import styles
import "./Manage.css";
import { AgGridReact } from "ag-grid-react";
import { SemContext } from "../timetable/provider/SemProvider";

const ManageClass = (props) => {
  //#region Component State
  const context = useContext(SemContext);
  const { updateGroupedOptions } = context;

  const [gridApi, setGridApi] = useState(null);
  const [gridColumnApi, setGridColumnApi] = useState(null);
  const [selectedRows, setSelectedRows] = useState([]);
  const [rowData, setRowData] = useState([]);

  const [classObj, setClassObj] = useState({});
  const [facultiesObj, setFacultiesObj] = useState({});
  const [currentClassId, setCurrentClassId] = useState();
  const [isLoading, setIsLoading] = useState(true);
  const [excelLoadedItems, setExcelLoadedItems] = useState([]);

  const gridOptions = {
    columnDefs: [
      {
        field: "id",
        headerName: "STT",
        checkboxSelection: true,
        headerCheckboxSelection: true,
        editable: false,
        maxWidth: 120,
      },
      { field: "faculty", headerName: "Khoa", rowGroup: true },
      { field: "className", headerName: "Tn Lp" },
      { field: "classType", headerName: "H" },
      { field: "classSize", headerName: "S S Lp" },
    ],
    defaultColDef: {
      flex: 1,
    },
    rowSelection: "multiple",
    groupUseEntireRow: true,
  };

  const contextMenuItems = (params) => [
    "copy",
    "copyWithHeaders",
    "separator",
    "export",
  ];

  //#endregion

  //#region Components

  const LectureItemToAdd = ({ onAdd, index, className, faculty }) => {
    return (
      <li
        key={index}
        className="excel__list-li_item"
        style={{ gridTemplateColumns: "4fr 3fr 1fr" }}
      >
        <div>
          <p>{className}</p>
        </div>
        <div>
          <p>{getFacID(faculty)}</p>
        </div>
        <span className="btn__trash add" onClick={onAdd}>
          <i className="far fa-plus-square" />
        </span>
      </li>
    );
  };

  //#endregion

  //#region Hooks

  // useEffect(() => {
  //   console.log(rowData);
  // }, [rowData]);

  useEffect(() => {
    async function fetchData() {
      var [allClasses, allFaculties] = await Promise.all([
        getAllClasses(props.semId),
        getAllFaculties(),
      ]);

      const classRow = Object.keys(allClasses).map((classKey, index) => {
        return {
          id: index + 1,
          classId: classKey,
          className: allClasses[classKey]["className"],
          classSize: allClasses[classKey]["classSize"],
          classType: allClasses[classKey]["classType"],
          faculty: allClasses[classKey]["faculty"],
        };
      });

      setRowData(classRow);
      setClassObj(allClasses);
      setFacultiesObj(allFaculties);
    }
    if (isLoading) {
      fetchData().then(() => setIsLoading(false));
    }
    // eslint-disable-next-line
  }, [isLoading]);

  function onGridReady(params) {
    setGridApi(params.api);
    setGridColumnApi(params.columnApi);
  }

  // useEffect(() => {
  //   console.log(classObj);
  // }, [classObj]);

  // useEffect(() => {
  //   console.log({ ...props, classObj });
  // }, [props, classObj]);

  //#endregion

  //#region Component Method

  function handleOnAdd({
    values,
    shouldRemoveChild = false,
    event = undefined,
  }) {
    // remove li element on add
    const removeLi = () => {
      const thisLi = event.target.closest("li");
      let nodes = Array.from(thisLi.closest("ul").children); // get array
      let index = nodes.indexOf(thisLi);
      if (index >= 0) {
        const tempArr = [...excelLoadedItems];
        tempArr.splice(index, 1);
        setExcelLoadedItems(tempArr);
      }
    };

    return new Promise((resolve, reject) => {
      if (!!values["className"]) {
        if (
          exists(values["className"]) &&
          exists(classObj[values["faculty"]]) &&
          values["className"] in classObj[values["faculty"]]
        ) {
          confirmAlert({
            title: "Lp ny  tn ti trong c s d liu",
            message: "Ban mun thay i thng tin ca lp ny?",
            buttons: [
              {
                className: "confirm__cancel",
                label: "Huy",
                onClick: () => reject(),
              },
              {
                className: "new",
                label: "Thm",
                onClick: () => {
                  newClass(props.semId, values);
                  setIsLoading(true);
                  shouldRemoveChild && removeLi();
                  resolve();
                },
              },
            ],
          });
        } else {
          newClass(props.semId, values);
          setIsLoading(true);
          shouldRemoveChild && removeLi();
          resolve();
        }
        updateGroupedOptions();
      }
    });
  }

  function handleOnModify(classID, values) {
    modifyClass(props.semId, classID, values)
      .then(() => defaultSuccessCB())
      .catch((rej) => defaultFailCB(rej));
  }

  const handleOnRemove = () => {
    confirmAlert({
      title: `Bn c chc mun xa ${selectedRows.length} lp nay?`,
      message: "Ban se khng th truy cp lai thng tin nay",
      buttons: [
        {
          className: "confirm__cancel",
          label: "Huy",
        },
        {
          className: "sign-out",
          label: "Xoa",
          onClick: () => {
            selectedRows.forEach((row) => {
              removeClass(props.semId, row?.classId);
            });
            setSelectedRows([]);
            setIsLoading(true);
          },
        },
      ],
    });
  };

  async function handleDropped(files) {
    const objHeaders = ["faculty", "className", "classType", "classSize"];
    const file = files[0];
    try {
      const data = await readExcel(file, objHeaders);
      if (data.length === 0) {
        setExcelLoadedItems(null);
      } else {
        setExcelLoadedItems(data);
        console.log(data);
      }
    } catch (err) {
      defaultFailCB(err);
    }
  }

  const handleDownloadTemplateFile = () =>
    window.open(
      "https://drive.google.com/file/d/19AczyMhfMR7AL0oAup2HYKkgGG-vUB_G/view?usp=sharing",
      "_blank",
      ""
    );

  //#endregion

  return (
    <>
      <div className="form-list__container">
        <div style={{ flex: "1" }}>
          {
            // render cancel button if excelLoaded has at least 1 row
            !!excelLoadedItems.length && (
              <Button
                style={{ marginBottom: 10 }}
                className="delete"
                // clear items
                onClick={() => setExcelLoadedItems([])}
              >
                Hy
              </Button>
            )
          }
          {
            // render excel loaded list items if it has at least 1 row or render dropzone_container
            !!excelLoadedItems.length ? (
              <ul id="excel__loaded-ul">
                {excelLoadedItems.map((values, index) => (
                  <LectureItemToAdd
                    index={index}
                    className={values["className"]}
                    faculty={values["faculty"]}
                    onAdd={(event) =>
                      handleOnAdd({ values, shouldRemoveChild: true, event })
                    }
                  />
                ))}
              </ul>
            ) : (
              <FileDropzone
                {...{
                  excelLoadedItems,
                  handleDropped,
                  handleDownloadTemplateFile,
                }}
              />
            )
          }
          {
            // remove form if list item has item
            !!excelLoadedItems.length || (
              <FormClassManage
                {...{
                  facultiesObj,
                  classObj,
                  currentClassId,
                  setCurrentClassId,
                  handleOnAdd,
                  handleOnModify,
                  setIsLoading,
                }}
              />
            )
          }
          {!!selectedRows.length && (
            <Button
              style={{ marginTop: 10 }}
              className="delete"
              // clear items
              onClick={handleOnRemove}
            >
              Xa {selectedRows.length} Khoa
            </Button>
          )}
        </div>
        <div style={{ marginLeft: 30, flex: "3" }}>
          <input
            type="text"
            className="text__search"
            onChange={({ target }) => gridApi.setQuickFilter(target.value)}
            placeholder="Tm kim..."
          />
          <div className="ag-theme-alpine-dark" style={{ height: "94%" }}>
            <AgGridReact
              {...{ onGridReady, rowData, gridOptions }}
              columnDefs={gridOptions["columnDefs"]}
              defaultColDef={{ flex: 1 }}
              enableRangeSelection={true}
              pagination={true}
              paginationPageSize={15}
              rowMultiSelectWithClick={true}
              suppressRowClickSelection={true}
              getContextMenuItems={contextMenuItems}
              overlayNoRowsTemplate={
                "Bn cha c d liu no trong c s d liu"
              }
              onRowClicked={({ data }) => {
                setCurrentClassId(data?.classId);
              }}
              onSelectionChanged={() => {
                var selectedRows = gridApi.getSelectedRows();
                setSelectedRows(selectedRows);
              }}
            />
          </div>
        </div>
      </div>
      {isLoading && <Loading />}
    </>
  );
};

export default ManageClass;
