import React, { useState, useEffect } from "react";

// import components
import { Button } from "../../Components";
import { Form } from "semantic-ui-react";

const FormLectures = ({
  handleOnModify,
  handleOnAdd,
  currentLectureId,
  lecturesObj,
  facultiesObj,
  setCurrentLectureId,
}) => {
  const initialState = {
    "lecture-name": "",
    "lecture-email": "",
    // faculty: "--- Khoa ---",
    faculty: "defaultValue",
  };

  const [values, setValues] = useState(initialState);

  useEffect(() => {
    if (currentLectureId === "") {
      setValues({ ...initialState });
    } else {
      setValues({ ...lecturesObj[currentLectureId] });
    }
    // eslint-disable-next-line
  }, [currentLectureId, lecturesObj]);

  // useEffect(() => {
  // 	console.log(currentLectureId, values);
  // }, [values, currentLectureId]);

  useEffect(() => {
    if (currentLectureId) {
      document.getElementById("lecture__select-faculty").value =
        lecturesObj[currentLectureId]["faculty"];
    }
  }, [currentLectureId, lecturesObj, facultiesObj]);

  const handleInputChange = (e) => {
    var { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const handleButtonAdd = async (e) => {
    e.preventDefault();
    await handleOnAdd({ values });
    setValues(initialState);
  };

  const handleButtonModify = async (e) => {
    e.preventDefault();
    const resolve = await handleOnModify(currentLectureId, values);
    resolve && setValues(initialState);
  };

  const cancelModify = () => {
    setCurrentLectureId("");
    setValues(initialState);
  };

  return (
    <>
      <Form id="manage__form">
        <Form.Input
          label="H tn"
          title="H tn ging vin"
          className="left-align"
          placeholder="H tn ging vin"
          type="text"
          name="lecture-name"
          id="lecture-name"
          value={values["lecture-name"]}
          onChange={handleInputChange}
          required
          autoFocus
        />
        <Form.Input
          label="Email"
          title="Email"
          placeholder="Email"
          id="lecture-email"
          type="email"
          name="lecture-email"
          value={values["lecture-email"]}
          onChange={handleInputChange}
        />
        <label htmlFor="lecture__select-faculty">Chn Khoa</label>
        <select
          title="Chn khoa"
          name="faculty"
          id="lecture__select-faculty"
          style={{ marginBottom: 10 }}
          onChange={handleInputChange}
          value={values.faculty}
        >
          <option disabled key="default" value="defaultValue">
            {Object.keys(facultiesObj).length
              ? "--- Khoa ---"
              : "Khng c khoa no"}
          </option>
          {Object.keys(facultiesObj).length &&
            Object.keys(facultiesObj).map((id) => {
              return (
                <option value={facultiesObj[id]["faculty-name"]} key={id}>
                  {facultiesObj[id]["faculty-name"]}
                </option>
              );
            })}
        </select>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div>
            {currentLectureId ? (
              <Button
                style={{ margin: 0, height: "100%" }}
                type="submit"
                className="new"
                title="Chnh sa khoa"
                onClick={handleButtonModify}
              >
                Chnh Sa
              </Button>
            ) : (
              <Button
                style={{ margin: "0", height: "100%" }}
                type="submit"
                className="new"
                title="Thm ging vin mi"
                onClick={handleButtonAdd}
              >
                Thm
              </Button>
            )}
          </div>
          <div>
            {currentLectureId && (
              <Button
                style={{ margin: "0", height: "100%" }}
                className="sign-out"
                title="Hy chnh sa"
                onClick={cancelModify}
              >
                Hy
              </Button>
            )}
          </div>
        </div>
      </Form>
    </>
  );
};

export default FormLectures;
