import React, { useState, useEffect } from "react";

// import components
import { Button } from "../../Components";
import { Form } from "semantic-ui-react";

const FormClassManage = ({
  facultiesObj,
  classObj,
  currentClassId,
  setCurrentClassId,
  handleOnAdd,
  handleOnModify,
  setIsLoading,
}) => {
  const initialState = {
    className: "",
    classSize: "",
    classType: "defaultValue",
    faculty: "defaultValue",
  };

  const [values, setValues] = useState(initialState);

  // useEffect(() => {
  //   console.log(values);
  // }, [values]);

  useEffect(() => {
    if (currentClassId === "") {
      setValues({ ...initialState });
    } else {
      setValues({ ...classObj[currentClassId] });
    }
    // eslint-disable-next-line
  }, [currentClassId, classObj]);

  const handleInputChange = (e) => {
    var { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const handleButtonAdd = async (e) => {
    e.preventDefault();
    await handleOnAdd({ values });
    setValues(initialState);
  };

  const handleButtonModify = (e) => {
    e.preventDefault();
    handleOnModify(currentClassId, values);
    setIsLoading(true);
    setValues(initialState);
  };

  const cancelModify = () => {
    setCurrentClassId("");
    setValues(initialState);
  };

  return (
    <>
      <Form id="manage__form" onSubmit={handleOnAdd}>
        <Form.Input
          label="Tn Lp"
          title="Tn Lp"
          className="left-align"
          placeholder="Tn Lp"
          type="text"
          name="className"
          value={values["className"]}
          onChange={handleInputChange}
          required
          autoFocus
        />
        <Form.Input
          label="S S Lp"
          title="S S Lp"
          className="left-align"
          placeholder="S S Lp"
          type="number"
          name="classSize"
          value={values["classSize"]}
          onChange={handleInputChange}
          required
          autoFocus
        />
        <label htmlFor="class__select-faculty">Chn H</label>
        <select
          title="Chn H"
          id="class__select-faculty"
          style={{ marginBottom: 10 }}
          name="classType"
          value={values["classType"]}
          onChange={handleInputChange}
        >
          <option disabled key="default" value="defaultValue">
            --- H ---
          </option>
          <option value="Cao ng" key="C">
            Cao ng
          </option>
          <option value="i Hc" key="H">
            i Hc
          </option>
        </select>
        <label htmlFor="class__select-class_type">Chn Khoa</label>
        <select
          title="Chn khoa"
          id="class__select-class_type"
          style={{ marginBottom: 10 }}
          name="faculty"
          value={values["faculty"]}
          onChange={handleInputChange}
        >
          <option disabled key="default" value="defaultValue">
            {Object.keys(facultiesObj).length
              ? "--- Khoa ---"
              : "Khng c khoa no"}
          </option>
          {Object.keys(facultiesObj).length &&
            Object.keys(facultiesObj).map((id) => {
              return (
                <option value={facultiesObj[id]["faculty-name"]} key={id}>
                  {facultiesObj[id]["faculty-name"]}
                </option>
              );
            })}
        </select>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <div>
            {currentClassId ? (
              <Button
                style={{ margin: 0, height: "100%" }}
                type="submit"
                className="new"
                title="Chnh sa khoa"
                onClick={handleButtonModify}
              >
                Chnh Sa
              </Button>
            ) : (
              <Button
                style={{ margin: "0", height: "100%" }}
                type="submit"
                className="new"
                title="Thm ging vin mi"
                onClick={handleButtonAdd}
              >
                Thm
              </Button>
            )}
          </div>
          <div>
            {currentClassId && (
              <Button
                style={{ margin: "0", height: "100%" }}
                className="sign-out"
                title="Hy chnh sa"
                onClick={cancelModify}
              >
                Hy
              </Button>
            )}
          </div>
        </div>
      </Form>
    </>
  );
};

export default FormClassManage;
