import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import components
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import MainFeaturedPost from './HeroImage';
import FeaturedPost from './FeaturedUser';
import { Typography } from '@material-ui/core';
import uhl from './uhl.jpg';
import NTM from './NTM.jpg';
import LAT from './LAT.jpg';

// import styles
import './Home.css';

const mainFeaturedPost = {
  title: 'I HC H LONG',
  description: 'Hc i i vi hnh | Hc  lm ngi | Hc  thnh cng',
  image: uhl,
  link: 'http://uhl.edu.vn/',
};

const featuredPosts = [
  {
    title: 'Nguyn Trng Minh',
    date: 'Thnh vin nhm',
    description: 'Ngi a phn mm t suy ngh thnh hin thc.',
    image: NTM,
    link: 'https://www.facebook.com/sipp.minhh/',
  },
  {
    title: 'L Anh T',
    date: 'Ging vin hng dn',
    description: 'Ngi vun gp  tng.',
    image: LAT,
    link: 'https://www.facebook.com/anhtucntt',
  },
  {
    title: 'Phm Nguyn Hng',
    date: 'Thnh vin nhm',
    description: 'Hu phng pha sau.',
    // image: LAT,
    link: 'https://www.facebook.com/profile.php?id=100032895618007',
  },
];

const Home = () => {
  return (
    <>
      <CssBaseline />
      <Container maxWidth="">
        <main>
          <MainFeaturedPost post={mainFeaturedPost} />
          <h1 id="title" align="center">
            Thnh Vin Nhm
          </h1>
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <p className="date">29-4-2020</p>
          </div>
          <Grid container spacing={4}>
            {featuredPosts.map(post => (
              <FeaturedPost key={post.title} post={post} />
            ))}
          </Grid>
        </main>
        <h1
          style={{
            margin: 20,
            color: '#c9d0d4',
            fontFamily: 'Helvetica Neue, sans-serif',
            fontSize: '46px',
          }}
          align="center"
        >
          Xin Cm n!
        </h1>
      </Container>
    </>
  );
};

export default Home;
